<template>
  <div>
    <!-- 面包屑导航区域 -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }">首页</el-breadcrumb-item>
      <el-breadcrumb-item>店铺管理</el-breadcrumb-item>
    </el-breadcrumb>
    <!-- 卡片视图区域 -->
    <el-card class="box-card">
      <div slot="header" class="clearfix">
        <span>店铺信息</span>
        <el-button style="float: right; padding: 3px 0" type="text" @click="changeType">修改</el-button>
      </div>
    <!-- 内容主体区域-->
      <el-form :model="shopForm" :rules="shopFormRules" ref="shopFormRef" label-width="80px">
        <el-form-item label="店铺名称" prop="store_name">
          <el-input v-model="shopForm.store_name" :disabled="isdisabled"></el-input>
        </el-form-item>
        <el-form-item label="店铺地址" prop="store_position">
          <el-input autosize v-model="shopForm.store_position" :disabled="isdisabled"></el-input>
        </el-form-item>
        <el-form-item label="移动电话" prop="store_phone_num">
          <el-input v-model="shopForm.store_phone_num" :disabled="isdisabled"></el-input>
        </el-form-item>
        <el-form-item label="固定电话" prop="store_telephone">
          <el-input v-model="shopForm.store_telephone" :disabled="isdisabled"></el-input>
        </el-form-item>
        <el-form-item label="创建时间" prop="created">
          <el-input v-model="shopForm.created" disabled></el-input>
        </el-form-item>
      </el-form>
      <span class="bottombtn" :hidden="isdisabled">
        <el-button  type="info"  @click="changeType()">取 消</el-button>
        <el-button  type="primary" @click="editShop()">保 存</el-button>
      </span>
    </el-card>
  </div>
</template>

<script>
export default {
  data () {
    return {
      // 获取用户列表的参数对象
      isdisabled: true,
      // 添加用户的表单数据
      shopForm: {
        store_name: '',
        store_position: '',
        store_phone_num: '',
        store_telephone: '',
        created: ''
      },
      created () {
        this.getshopInfo()
      },
      // 添加表单的验证规则对象
      shopFormRules: {
        store_name: [
          { required: true, message: '请输入店铺名', trigger: 'blur' }
        ],
        store_position: [
          { required: true, message: '请输入店铺地址', trigger: 'blur' }
        ],
        store_phone_num: [
          { required: true, message: '请输入店铺移动电话', trigger: 'blur' }
        ],
        store_telephone: [
          { required: true, message: '请输入店铺联系方式', trigger: 'blur' }
        ],
        created: [
          { required: true, message: '请输入店铺成立时间', trigger: 'blur' }
        ]
      }
    }
  },
  created () {
    this.getshopInfo()
  },
  methods: {
    async getshopInfo () {
      // 获取店铺信息请求
      const { data: res } = await this.axios.post('/selectStoreInfo', { email: window.sessionStorage.getItem('email') })
      console.log(res)
      if (res.status !== '1') {
        return this.$message.error('获取店铺信息失败')
      }
      // this.$message.success('获取店铺信息成功')
      this.shopForm = res.data[0]
      window.sessionStorage.setItem('storeid', res.data[0].store_id)
      console.log(res)
    },
    changeType () {
      this.isdisabled = !this.isdisabled
      this.getshopInfo()
    },
    async editShop () {
      const { data: res } = await this.axios.post('/modifyStoreInfo', {
        store_id: window.sessionStorage.getItem('storeid'),
        store_name: this.shopForm.store_name,
        store_position: this.shopForm.store_position,
        store_phone_num: this.shopForm.store_phone_num,
        store_telephone: this.shopForm.store_telephone
      })
      console.log(res)
      if (res.status !== '1') {
        this.$message.error('修改店铺信息失败')
        this.isdisabled = !this.disabled
        this.getshopInfo()
      }
      // this.$message.success('修改店铺信息成功')
      this.isdisabled = !this.disabled
      this.getshopInfo()
    }
  }
}
</script>

<style lang="less" scoped>
.bottombtn {
  float: right;
  margin-bottom: 10px;
}
</style>
