/* eslint-disable no-irregular-whitespace */
<template>
  <div>
    <!-- 面包屑导航区域 -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }">首页</el-breadcrumb-item>
      <el-breadcrumb-item>图书借读</el-breadcrumb-item>
    </el-breadcrumb>

    <!-- 卡片视图区域 -->
    <el-card class="box-card">

       <!-- 图书列表区 -->
       <el-table :data="booklist" border stripe>
         <el-table-column label="用户邮箱" prop="user_email"></el-table-column>
         <el-table-column label="图书ID" prop="book_id"></el-table-column>
         <el-table-column label="图书名称" prop="book_name"></el-table-column>
         <el-table-column label="借读时间" prop="lend_time"></el-table-column>
         <el-table-column label="联系方式" prop="telephone"></el-table-column>
       </el-table>
        <!-- 分页区域 -->
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="queryInfo.pagenum"
        :page-sizes="[1,2, 5, 10]"
        :page-size="queryInfo.pagesize"
        layout="total,sizes,prev, pager, next, jumper"
        :total="total">
      </el-pagination>
    </el-card>

  </div>
</template>

<script>
export default {
  data () {
    return {
      // 获取用户列表的参数对象
      queryInfo: {
        // 当前页数
        pagenum: 1,
        // 当前每页显示多少条数据
        pagesize: 5,
        query: ''
      },
      booklist: [],
      total: 0
    }
  },
  created () {
    this.getBookList()
  },
  methods: {

    // 获取图书列表
    async getBookList () {
      // 获取的请求
      console.log(this.queryInfo.pagesize)
      const { data: res } = await this.axios.post('/selectlendbook', {
        pagenum: this.queryInfo.pagenum,
        pagesize: this.queryInfo.pagesize
      })
      console.log(res)
      if (res.status !== '1') return this.$message.error('获取借读图书列表失败')
      this.$message.success('获取借读图书列表成功')
      this.booklist = res.data
      this.total = parseInt(res.sum)
    },
    // 监听pagesize改变
    handleSizeChange (newSize) {
      console.log(newSize)
      this.queryInfo.pagesize = newSize
      this.getBookList()
    },
    // 监听页码值改变的事件
    handleCurrentChange (newPage) {
      console.log(newPage)
      this.queryInfo.pagenum = newPage
      this.getBookList()
    }
  }
}
</script>

<style lang="less" scoped>
.editbtn{
  float: right;
  line-height: 15px;
}
</style>
