<template>
  <div>
    <!-- 面包屑导航区域 -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }">首页</el-breadcrumb-item>
      <el-breadcrumb-item>图书管理</el-breadcrumb-item>
      <el-breadcrumb-item>图书分类</el-breadcrumb-item>
    </el-breadcrumb>
    <!-- 卡片视图区域 -->
    <el-card class="box-card">
       <!-- 类别列表区 -->
       <el-table :data="catelist" border stripe>
         <el-table-column label="类别" prop="categoryid"></el-table-column>
         <el-table-column label="类别介绍" prop="cname"></el-table-column>
         <el-table-column label="操作" width="140px">
            <template slot-scope="scope">
             <!-- 查看该类别下的图书按钮 -->
             <el-tooltip class="item" effect="dark" content="查看该类别下的图书" placement="top" :enterable="false">
             <el-button type="primary" icon="el-icon-folder-opened" size="mini" @click="getBookList(scope.row.categoryid)"></el-button>
             </el-tooltip>
           </template>
         </el-table-column>
       </el-table>
    </el-card>

    <!-- 类别图书对话框 -->
    <el-dialog
      title="该类别下的图书"
      :visible.sync="infodialogVisible"
      width="65%" @close="infoDialogClosed">
      <!-- 内容主体区域-->
      <el-card class="box-card">
       <!-- 订单列表区 -->
       <el-table :data="booklist" border stripe>
         <el-table-column label="图书ID" prop="bookid"></el-table-column>
         <el-table-column label="图书名称" prop="bookname"></el-table-column>
       </el-table>

       <!-- 分页区域 -->
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="queryInfo.pagenum"
        :page-sizes="[1,2, 5, 10]"
        :page-size="queryInfo.pagesize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total">
      </el-pagination>
    </el-card>
    </el-dialog>
  </div>
</template>

<script>
export default {
  data () {
    return {
      catelist: [],
      booklist: [],
      queryInfo: {
        store_id: window.sessionStorage.getItem('storeid'),
        // 当前页数
        pagenum: 1,
        // 当前每页显示多少条数据
        pagesize: 5
      },
      infoForm: {
        bookid: '',
        bookname: ''
      },
      total: 0,
      infodialogVisible: false
    }
  },
  created () {
    this.getcateList()
  },
  methods: {
    async getcateList () {
      const { data: res } = await this.axios.post('/selectBookCate')
      if (res.status !== '1') {
        return this.$message.error('获取类别列表失败')
      }
      this.$message.success('获取类别列表成功')
      this.catelist = res.data
    },

    async getBookList (id) {
      this.infodialogVisible = true
      window.sessionStorage.setItem('cateid', id)
      const { data: res } = await this.axios.post('/selectBookByCate', {
        category_id: id,
        store_id: window.sessionStorage.getItem('storeid'),
        // store_id: 1,
        pagenum: this.queryInfo.pagenum,
        pagesize: this.queryInfo.pagesize
      })
      console.log(res)
      if (res.status !== '1') {
        return this.$message.error('获取该类别图书列表失败')
      }
      this.$message.success('获取该类别图书列表成功')
      this.booklist = res.data
      this.total = parseInt(res.sum)
      console.log(res)
    },
    async getBookListin () {
      this.infodialogVisible = true
      const { data: res } = await this.axios.post('/selectBookByCate', {
        category_id: window.sessionStorage.getItem('cateid'),
        // store_id: window.sessionStorage.getItem('storeid'),
        store_id: 1,
        pagenum: this.queryInfo.pagenum,
        pagesize: this.queryInfo.pagesize
      })
      console.log(res)
      if (res.status !== '1') {
        return this.$message.error('获取该类别图书列表失败')
      }
      this.$message.success('获取该类别图书列表成功')
      this.booklist = res.data
      this.total = parseInt(res.sum)
    },
    // 监听pagesize改变
    handleSizeChange (newSize) {
      console.log(newSize)
      this.queryInfo.pagesize = newSize
      this.getBookListin()
    },
    // 监听页码值改变的事件
    handleCurrentChange (newPage) {
      console.log(newPage)
      this.queryInfo.pagenum = newPage
      this.getBookListin()
    }
  }
}
</script>

<style lang="less" scoped>

</style>
