<template>
  <div>
    <!-- 面包屑导航区域 -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }">首页</el-breadcrumb-item>
      <el-breadcrumb-item>订单管理</el-breadcrumb-item>
      <el-breadcrumb-item>订单列表</el-breadcrumb-item>
    </el-breadcrumb>

    <!-- 卡片视图区域 -->
    <el-card class="box-card">
      <!-- 搜索与添加区域 -->
       <el-row gutter="20">
         <el-col :span="8">
           <el-input placeholder="请输入内容" v-model="queryInfo.query" clearable @clear="getOrderList">
             <el-button slot="append" icon="el-icon-search" @click="selectOrdersQuery"></el-button>
          </el-input>
          </el-col>
          <el-col :span="8">
            <el-select v-model="queryInfo.qstatus" clearable @clear="getOrderList" placeholder="选择状态查询 ">
            <el-option
              v-for="item in status"
              :key="item.value"
              :label="item.label"
              :value="item.value"
              :hidden="item.hidden">
            </el-option>
            </el-select>
            <el-button icon="el-icon-search" @click="selectOrdersStatus"></el-button>
          </el-col>
       </el-row>

       <!-- 订单列表区 -->
       <el-table :data="orderlist" border stripe>
         <el-table-column label="订单ID" prop="orderid"></el-table-column>
         <el-table-column label="用户名称" prop="username"></el-table-column>
         <el-table-column label="图书名称" prop="bookname"></el-table-column>
         <el-table-column label="价格" prop="payment"></el-table-column>
         <el-table-column label="创建时间" prop="create_time"></el-table-column>
         <el-table-column label="订单状态" prop="status">
           <template slot-scope="scope">
             <span style="float:center"> 当前状态：{{status[scope.row.status].label}}</span>
             <div style="color: #409EFF;cursor:pointer;float:center" @click="changestatus(scope.row)">修改状态</div>
           </template>
         </el-table-column>
         <el-table-column label="操作" width="180px">
            <template slot-scope="scope">
             <!-- 修改按钮 -->
             <el-tooltip class="item" effect="dark" content="编辑订单" placement="top" :enterable="false">
             <el-button type="primary" icon="el-icon-edit" size="mini" content="编辑订单" @click="showEditDialog(scope.row.orderid)"></el-button>
             </el-tooltip>
             <!-- 删除按钮 -->
             <el-tooltip class="item" effect="dark" content="删除订单" placement="top" :enterable="false">
             <el-button type="danger" icon="el-icon-delete" size="mini" @click="removeOrderbyId(scope.row.orderid)"></el-button>
             </el-tooltip>
             <!-- 导出订单 -->
             <el-tooltip class="item" effect="dark" content="导出订单" placement="top" :enterable="false">
               <el-button type="warning" icon="el-icon-more" size="mini" @click="showinfoDialog(scope.row.orderid)"></el-button>
             </el-tooltip>
           </template>
         </el-table-column>
       </el-table>

       <!-- 分页区域 -->
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="queryInfo.pagenum"
        :page-sizes="[1,2,5, 10]"
        :page-size="queryInfo.pagesize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total">
      </el-pagination>
    </el-card>

    <!-- 修改对话框 -->
    <el-dialog
      title="修改订单"
      :visible.sync="editdialogVisible"
      width="50%" @close="editDialogClosed">
      <!-- 内容主体区域-->
      <el-form :model="editForm" :rules="editFormRules" ref="editFormRef" label-width="120px">
        <el-form-item label="订单ID" prop="orderid">
          <el-input autosize v-model="editForm.orderid" disabled></el-input>
        </el-form-item>
        <el-form-item label="收件人姓名" prop="receiver_name">
          <el-input autosize v-model="editForm.receiver_name"></el-input>
        </el-form-item>
        <el-form-item label="收件人电话" prop="receiver_mobile">
          <el-input autosize v-model="editForm.receiver_mobile"></el-input>
        </el-form-item>
        <el-form-item label="收货省市" prop="receiver_state">
          <el-input autosize v-model="editForm.receiver_state"></el-input>
        </el-form-item>
        <el-form-item label="收货城市" prop="receiver_city" >
          <el-input autosize v-model="editForm.receiver_city"></el-input>
        </el-form-item>
        <el-form-item label="收货地区" prop="receiver_district">
          <el-input autosize v-model="editForm.receiver_district"></el-input>
        </el-form-item>
        <el-form-item label="收货地址" prop="receiver_address">
          <el-input autosize v-model="editForm.receiver_address"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="editdialogVisible = false">取 消</el-button>
        <el-button type="primary" @click="editBookInfo">确 定</el-button>
     </span>
    </el-dialog>

    <!-- 修改状态对话框 -->
    <el-dialog
      title="修改状态"
      :visible.sync="editstatusdialogVisible"
      width="50%" @close="editstatusDialogClosed">
      <!-- 内容主体区域-->
      <el-form :model="editstatusForm" :rules="editstatusFormRules" ref="editstatusFormRef" label-width="120px">
        <el-form-item label="订单ID" prop="newstatus">
           <el-select v-model="editstatusForm.newstatus" clearable placeholder="请选择 ">
            <el-option
              v-for="item in options"
              :key="item.value"
              :label="item.label"
              :value="item.value"
              :disabled="item.disabled">
            </el-option>
          </el-select>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="canclechangestatus">取 消</el-button>
        <el-button type="primary" @click="confirmchangeStatus">确 定</el-button>
     </span>
    </el-dialog>

    <!-- 导出对话框 -->
    <el-dialog
      title="导出订单"
      :visible.sync="infodialogVisible"
      width="50%" @close="infoDialogClosed">
      <!-- 内容主体区域-->
      <div id="exportPdf" ref="exportPdf">
        <el-form :model="infoForm" ref="infoFormRef" label-width="100px">
          <el-form-item label="订单ID" prop="orderid">
            <el-input autosize v-model="infoForm.orderid" disabled></el-input>
          </el-form-item>
          <el-form-item label="收件人姓名" prop="receiver_name">
            <el-input autosize v-model="infoForm.receiver_name" disabled></el-input>
          </el-form-item>
          <el-form-item label="收件人电话" prop="receiver_mobile">
            <el-input autosize v-model="infoForm.receiver_mobile" disabled></el-input>
          </el-form-item>
          <el-form-item label="收货省市" prop="receiver_state">
            <el-input autosize v-model="infoForm.receiver_state" disabled></el-input>
          </el-form-item>
          <el-form-item label="收货城市" prop="receiver_city" >
            <el-input autosize v-model="infoForm.receiver_city" disabled></el-input>
          </el-form-item>
          <el-form-item label="收货地区" prop="receiver_district">
            <el-input autosize v-model="infoForm.receiver_district" disabled></el-input>
          </el-form-item>
          <el-form-item label="收货地址" prop="receiver_address">
            <el-input autosize v-model="infoForm.receiver_address" disabled></el-input>
          </el-form-item>
        </el-form>
      </div>
      <span slot="footer" class="dialog-footer">
        <el-button @click="infodialogVisible = false">取 消</el-button>
        <el-button type="primary" @click="savePdf()">导 出</el-button>
     </span>
    </el-dialog>
  </div>
</template>

<script>
export default {
  data () {
    return {
      // 状态选项value
      status: [{
        value: '0',
        label: '已提交'
      }, {
        value: '1',
        label: '已付款'
      }, {
        value: '2',
        label: '已发货'
      }, {
        value: '3',
        label: '已收货'
      }, {
        value: '4',
        label: '交易完成'
      }, {
        value: '5',
        label: '交易完成',
        hidden: true
      }, {
        value: '6',
        label: '交易完成',
        hidden: true
      }, {
        value: '7',
        label: '取消订单'
      }],
      options: [{}],
      // 状态选项value
      options1: [{
        value: '0',
        label: '已提交'
      }, {
        value: '1',
        label: '已付款'
      }, {
        value: '7',
        label: '取消订单'
      }],
      options2: [{
        value: '1',
        label: '已付款'
      }, {
        value: '2',
        label: '已发货'
      }, {
        value: '7',
        label: '取消订单'
      }],
      options3: [{
        value: '2',
        label: '已发货'
      }, {
        value: '3',
        label: '已收货'
      }],
      options4: [{
        value: '3',
        label: '已收货'
      }, {
        value: '4',
        label: '交易完成'
      }],
      options5: [{
        value: '4',
        label: '交易完成'
      }],
      options6: [{
        value: '5',
        label: '交易完成'
      }],
      value: '',
      // 获取用订单列表的参数对象
      queryInfo: {
        query: '',
        store_id: window.sessionStorage.getItem('storeid'),
        // 当前页数
        pagenum: 1,
        // 当前每页显示多少条数据
        pagesize: 5,
        qstatus: ''
      },
      orderlist: [],
      total: 0,
      // 控制添加对话框的显示和隐藏
      editdialogVisible: false,
      infodialogVisible: false,
      editstatusdialogVisible: false,

      editstatusForm: {
        newstatus: ''
      },
      // 编辑订单的表单数据
      editForm: {
        orderid: '',
        receiver_name: '',
        receiver_mobile: '',
        receiver_state: '',
        receiver_city: '',
        receiver_district: '',
        receiver_address: ''
      },
      infoForm: {
        orderid: '',
        receiver_name: '',
        receiver_mobile: '',
        receiver_state: '',
        receiver_city: '',
        receiver_district: '',
        receiver_address: ''
      },
      // 添加表单的验证规则对象
      editFormRules: {
        receiver_name: [
          { required: true, message: '请输入收件人姓名', trigger: 'blur' }
        ],
        receiver_mobile: [
          { required: true, message: '请输入收件人电话', trigger: 'blur' }
        ],
        receiver_state: [
          { required: true, message: '请输入收件省市', trigger: 'blur' }
        ],
        receiver_city: [
          { required: true, message: '请输入收件城市', trigger: 'blur' }
        ],
        receiver_district: [
          { required: true, message: '请输入收件地区', trigger: 'blur' }
        ],
        receiver_address: [
          { required: true, message: '请输入收件地址', trigger: 'blur' }
        ]
      },
      editstatusFormRules: {
        newstatus: [
          { required: true, message: '请输入订单状态', trigger: 'blur' }
        ]
      }
    }
  },
  created () {
    this.getOrderList()
  },
  methods: {
    // 取消修改状态
    canclechangestatus () {
      this.editstatusdialogVisible = false
    },
    // 导出订单
    savePdf () {
      this.$PDFSave(this.$refs.exportPdf, window.sessionStorage.getItem('orderid'))
      this.$message.success('导出成功')
    },
    // 获取订单
    async getOrderList () {
      const { data: res } = await this.axios.post('/selectOrders', {
        store_id: this.queryInfo.store_id,
        pagenum: this.queryInfo.pagenum,
        pagesize: this.queryInfo.pagesize
      })
      console.log(res)
      if (res.status !== '1') {
        return this.$message.error('获取订单列表失败')
      }
      console.log(res)
      this.$message.success('获取订单列表成功')
      this.orderlist = res.data
      this.total = parseInt(res.sum)
    },
    // 模糊查询订单
    async selectOrdersQuery () {
      const { data: res } = await this.axios.post('/selectOrdersQuery', {
        store_id: this.queryInfo.store_id,
        pagenum: this.queryInfo.pagenum,
        pagesize: this.queryInfo.pagesize,
        query: this.queryInfo.query
      })
      console.log(res)
      if (res.status !== '1') {
        return this.$message.error('获取订单列表失败')
      }
      this.$message.success('获取订单列表成功')
      this.orderlist = res.data
      console.log(status)
      this.total = parseInt(res.sum)
    },
    // 根据状态查询订单
    async selectOrdersStatus () {
      const { data: res } = await this.axios.post('/selectOrdersStatus', {
        store_id: this.queryInfo.store_id,
        pagenum: this.queryInfo.pagenum,
        pagesize: this.queryInfo.pagesize,
        status: this.queryInfo.qstatus
      })
      console.log(res)
      if (res.status !== '1') {
        return this.$message.error('获取订单列表失败')
      }
      this.$message.success('获取订单列表成功')
      this.orderlist = res.data
      console.log(status)
      this.total = parseInt(res.sum)
    },
    // 修改状态弹框
    changestatus (order) {
      window.sessionStorage.setItem('orderid', order.orderid)
      console.log(order.status)
      if (order.status === 0) {
        this.options = this.options1
        this.editstatusdialogVisible = true
      } else if (order.status === 1) {
        this.options = this.options2
        this.editstatusdialogVisible = true
      } else if (order.status === 2) {
        this.options = this.options3
        this.editstatusdialogVisible = true
      } else if (order.status === 3) {
        this.options = this.options4
        this.editstatusdialogVisible = true
      } else if (order.status === 4) {
        this.editstatusdialogVisible = false
        this.$message.error('已完成订单不可再修改状态')
      } else if (order.status === 5) {
        this.editstatusdialogVisible = false
        this.$message.error('已完成订单不可再修改状态')
      } else if (order.status === 7) {
        this.editstatusdialogVisible = false
        this.$message.error('已取消订单不可再修改状态')
      }
    },
    // 修改状态请求
    async confirmchangeStatus () {
      const { data: res } = await this.axios.post('/modifyOrderStatus', {
        order_id: window.sessionStorage.getItem('orderid'),
        status: this.editstatusForm.newstatus
      })
      console.log(window.sessionStorage.getItem('orderid'))
      console.log(this.editstatusForm.newstatus)
      console.log(res)
      if (res.status !== '1') return this.$message.error('修改状态失败')
      this.$message.success('修改状态成功')
      this.editstatusdialogVisible = false
      this.getOrderList()
    },
    // 监听pagesize改变
    handleSizeChange (newSize) {
      console.log(newSize)
      this.queryInfo.pagesize = newSize
      this.getOrderList()
    },
    // 监听页码值改变的事件
    handleCurrentChange (newPage) {
      console.log(newPage)
      this.queryInfo.pagenum = newPage
      this.getOrderList()
    },
    // 展示编辑订单的对话框
    async showEditDialog (id) {
      this.editdialogVisible = true
      console.log(id)
      const { data: res } = await this.axios.post('/selectOrderShipping', {
        order_id: id
      })
      if (res.status !== '1') {
        // return this.$message.error('查询订单信息失败')
      }
      this.editForm.orderid = res.data[0].order_id
      this.editForm.receiver_name = res.data[0].receiver_name
      this.editForm.receiver_mobile = res.data[0].receiver_mobile
      this.editForm.receiver_state = res.data[0].receiver_state
      this.editForm.receiver_city = res.data[0].receiver_city
      this.editForm.receiver_district = res.data[0].receiver_district
      this.editForm.receiver_address = res.data[0].receiver_address
    },
    // 展示导出订单的对话框
    async showinfoDialog (id) {
      window.sessionStorage.setItem('orderid', id)
      this.infodialogVisible = true
      console.log(id)
      console.log(this.infodialogVisible)
      const { data: res } = await this.axios.post('/selectOrderShipping', {
        order_id: id
      })
      if (res.status !== '1') {
        return this.$message.error('查询订单信息失败')
      }
      this.$message.success('查询订单信息成功')
      console.log(res)
      this.infoForm.orderid = res.data[0].order_id
      this.infoForm.receiver_name = res.data[0].receiver_name
      this.infoForm.receiver_mobile = res.data[0].receiver_mobile
      this.infoForm.receiver_state = res.data[0].receiver_state
      this.infoForm.receiver_city = res.data[0].receiver_city
      this.infoForm.receiver_district = res.data[0].receiver_district
      this.infoForm.receiver_address = res.data[0].receiver_address
    },
    // 导出订单
    exportorder (id) {
      this.infodialogVisible = false
      this.$message.success('导出操作')
    },
    // 监听编辑订单的关闭事件
    editDialogClosed () {
      this.$refs.editFormRef.resetFields()
    },
    // 监听修改状态的关闭事件
    editstatusDialogClosed () {
      this.$refs.editstatusFormRef.resetFields()
    },
    // 监听导出订单对话框关闭
    infoDialogClosed () {
      this.$refs.infoFormRef.resetFields()
    },
    // 修改订单信息并提交
    editBookInfo () {
      this.$refs.editFormRef.validate(async valid => {
        if (!valid) return
        // 发起修改请求
        const { data: res } = await this.axios.post('/modifyOrderShipping', {
          order_id: this.editForm.orderid,
          receiver_name: this.editForm.receiver_name,
          receiver_mobile: this.editForm.receiver_mobile,
          receiver_state: this.editForm.receiver_state,
          receiver_city: this.editForm.receiver_city,
          receiver_district: this.editForm.receiver_district,
          receiver_address: this.editForm.receiver_address
        })
        if (res.status !== '1') {
          this.$message.error('修改失败')
        }
        console.log(res)
        // 关闭对话框
        this.editdialogVisible = false
        // 刷新数据列表
        this.getOrderList()
        // 提示修改成功
        this.$message.success('修改成功')
      })
    },
    // 根据ID删除
    async removeOrderbyId (id) {
      // 弹框询问用户
      const confirmresult = await this.$confirm('此操作将永久删除该订单, 是否继续?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).catch(err => err)
      if (confirmresult !== 'confirm') {
        return this.$message.info('已取消删除')
      }
      const { data: res } = await this.axios.post('/deleteOrder', {
        order_id: id
      })
      console.log(res)
      if (res.status !== '1') {
        return this.$message.error('删除失败')
      }
      this.$message.success('删除成功')
      this.getOrderList()
    }
  }
}
</script>

<style lang="less" scoped>

</style>
