<template>
  <div>
    <!-- 面包屑导航区域 -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }">首页</el-breadcrumb-item>
      <el-breadcrumb-item>数据统计</el-breadcrumb-item>
      <el-breadcrumb-item>图书排行</el-breadcrumb-item>
    </el-breadcrumb>
    <!-- 卡片视图区域 -->
    <el-card class="box-card">
       <!-- 类别列表区 -->
       <span>店铺最近一周图书销量排行</span>
       <el-table :data="ranklist">
         <el-table-column label="排名" type="index" width="95px"></el-table-column>
         <el-table-column label="图书名称" prop="bookname"></el-table-column>
       </el-table>
    </el-card>
  </div>
</template>

<script>
export default {
  data () {
    return {
      ranklist: {
        bookname: ''
      }
    }
  },
  created () {
    this.getrankList()
  },
  methods: {
    async getrankList () {
      const { data: res } = await this.axios.post('/selectStoreTop', {
        store_id: window.sessionStorage.getItem('storeid')
      })
      if (res.status !== '1') {
        return this.$message.error('获取数据失败')
      }
      console.log(res.data)
      this.ranklist = res.data
    }
  }
}
</script>

