<template>
    <div>
        <el-breadcrumb separator-class="el-icon-arrow-right">
          <el-breadcrumb-item :to="{ path: '/home' }">首页</el-breadcrumb-item>
          <el-breadcrumb-item>数据统计</el-breadcrumb-item>
          <el-breadcrumb-item>数据报表</el-breadcrumb-item>
        </el-breadcrumb>
        <el-card>
            <!-- 为 ECharts 准备一个具备大小（宽高）的 DOM -->
            <div id="main" style="width: 1000px;height:400px;"></div>
            <div id="second" style="width: 1000px;height:400px;"></div>
        </el-card>
    </div>
</template>
<script>
// 导入echrat
import echarts from 'echarts'
export default {
  data () {
    return {
      data: [],
      data2: []
    }
  },
  created () {},
  // 此时页面上的元素被渲染完毕了
  async mounted () {
    // 基于准备好的dom，初始化echarts实例
    this.data = []
    this.data2 = []
    var myChart = echarts.init(document.getElementById('main'))
    const { data: res } = await this.axios.post('/storeOrderSum', {
      store_id: window.sessionStorage.getItem('storeid')
    })
    if (res.status !== '1') {
      return this.$message.error('获取数据失败')
    }
    console.log(res.data)
    for (const item1 of res.data) {
      this.data.push(item1.日期)
      this.data2.push(item1.销量)
    }
    // 指定图表的配置项和数据
    var option = {
      title: {
        text: '该店铺一周内每日销量'
      },
      tooltip: {},
      legend: {
        data: ['销量']
      },
      xAxis: {
        data: this.data
      },
      yAxis: {},
      series: [{
        name: '销量',
        type: 'bar',
        data: this.data2
      }]
    }
    // 使用刚指定的配置项和数据显示图表。
    myChart.setOption(option)
  },
  methods () {}
}
</script>
