/* eslint-disable no-irregular-whitespace */
<template>
  <div>
    <!-- 面包屑导航区域 -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }">首页</el-breadcrumb-item>
      <el-breadcrumb-item>图书管理</el-breadcrumb-item>
      <el-breadcrumb-item>图书列表</el-breadcrumb-item>
    </el-breadcrumb>

    <!-- 卡片视图区域 -->
    <el-card class="box-card">
      <!-- 搜索与添加区域 -->
       <el-row gutter="20">
         <el-col :span="8">
           <el-input placeholder="请输入内容" v-model="queryInfo.query" clearable
           @clear="getBookList">
             <el-button slot="append" icon="el-icon-search" @click="selectBookQuery"></el-button>
          </el-input>
          </el-col>
          <el-col :span="4">
            <el-button type="primary" @click="addDialogVisible = true">添加图书</el-button>
          </el-col>
       </el-row>

       <!-- 图书列表区 -->
       <el-table :data="booklist" border stripe>
         <el-table-column label="图书ID" prop="bookid"></el-table-column>
         <el-table-column label="图书名称" prop="name"></el-table-column>
         <el-table-column label="出版社" prop="press"></el-table-column>
         <el-table-column label="作者" prop="author"></el-table-column>
         <el-table-column label="价格" prop="price"></el-table-column>
         <el-table-column label="状态(上/下架)" prop="is_shelf"></el-table-column>
         <el-table-column label="操作" width="180px">
            <template slot-scope="scope">
              <el-tooltip class="item" effect="dark" content="删除图书" placement="top" :enterable="false">
              <el-button type="danger" icon="el-icon-delete" size="mini" @click="removeBookbyId(scope.row.bookid)"></el-button>
              </el-tooltip>
             <!-- 查看详情 -->
             <el-tooltip class="item" effect="dark" content="查看详情" placement="top" :enterable="false">
               <el-button type="warning" icon="el-icon-more" size="mini" @click="showinfoDialog(scope.row.bookid)"></el-button>
             </el-tooltip>
           </template>
         </el-table-column>
       </el-table>
        <!-- 分页区域 -->
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="queryInfo.pagenum"
        :page-sizes="[1,2, 5, 10]"
        :page-size="queryInfo.pagesize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total">
      </el-pagination>
    </el-card>

    <!-- 添加对话框 -->
    <el-dialog
      title="添加图书"
      :visible.sync="addDialogVisible"
      width="50%" @close="addDialogClosed">
      <!-- 内容主体区域-->
      <el-form :model="addForm" ref="addFormRef" :rules="addFormRules" label-width="100px">
        <el-form-item style="margin-top:-30px">
        </el-form-item >
        <el-form-item label="图书名称" prop="name">
          <el-input type="textarea" autosize v-model="addForm.name" ></el-input>
        </el-form-item>
        <el-form-item label="图书简介" prop="outline">
          <el-input type="textarea" autosize v-model="addForm.outline"></el-input>
        </el-form-item>
        <el-form-item label="图书详情" prop="detail">
          <el-input type="textarea" autosize v-model="addForm.detail" ></el-input>
        </el-form-item>
        <el-form-item label="出版社" prop="press">
          <el-input type="textarea" autosize v-model="addForm.press" ></el-input>
        </el-form-item>
        <el-form-item label="出版时间" prop="publish_date">
           <el-input type="textarea" autosize v-model="addForm.publish_date" ></el-input>
        </el-form-item>
        <el-form-item label="图书大小" prop="size">
          <el-input type="textarea" autosize v-model="addForm.size"></el-input>
        </el-form-item>
        <el-form-item label="图书版本" prop="version">
          <el-input type="textarea" autosize v-model="addForm.version"></el-input>
        </el-form-item>
        <el-form-item label="作者" prop="author">
          <el-input type="textarea" autosize v-model="addForm.author"></el-input>
        </el-form-item>
        <el-form-item label="翻译者" prop="translator">
          <el-input type="textarea" autosize v-model="addForm.translator" ></el-input>
        </el-form-item>
        <el-form-item label="价格" prop="price">
          <el-input type="textarea" autosize v-model="addForm.price"></el-input>
        </el-form-item>
        <el-form-item label="总页数" prop="pages">
          <el-input type="textarea" autosize v-model="addForm.pages" ></el-input>
        </el-form-item>
        <el-form-item label="目录" prop="catalog">
          <el-input type="textarea" autosize v-model="addForm.catalog" ></el-input>
        </el-form-item>
        <el-form-item label="定价" prop="market_price">
          <el-input type="textarea" autosize v-model="addForm.market_price"></el-input>
        </el-form-item>
        <el-form-item label="会员价" prop="member_price">
          <el-input type="textarea" autosize v-model="addForm.member_price"></el-input>
        </el-form-item>
        <el-form-item label="折扣" prop="discount">
          <el-input type="textarea" autosize v-model="addForm.discount"></el-input>
        </el-form-item>
        <el-form-item label="图书图片" prop="image_url">
          <el-upload class="upload-demo"
            :action="null"
            :before-upload="beforeUpload"
            :on-preview="handlePreview"
            :on-remove="handleRemove"

            list-type="picture">
            <el-button size="small" type="primary">点击上传</el-button>
            <div slot="tip" class="el-upload__tip">只能上传jpg/png文件，且不超过500kb</div>
          </el-upload>
        </el-form-item>
        <el-form-item label="库存数量" prop="store_mount">
          <el-input type="textarea" autosize v-model="addForm.store_mount" ></el-input>
        </el-form-item>
        <el-form-item label="封装方式" prop="pack_style">
          <el-input type="textarea" autosize v-model="addForm.pack_style" ></el-input>
        </el-form-item>
        <el-form-item label="上架状态" prop="is_shelf">
         <el-select v-model="addForm.is_shelf" placeholder="选择图书上架状态" style="margin-right: 3%">
            <el-option
              v-for="item in options"
              :key="item.is_shelf"
              :label="item.label"
              :value="item.is_shelf">
            </el-option>
         </el-select>
        </el-form-item>
        <el-form-item label="类别ID" prop="book_category_id">
          <el-select v-model="addForm.book_category_id" placeholder="选择图书类别" style="margin-right: 3%">
            <el-option
              v-for="item in options1"
              :key="item.book_category_id"
              :label="item.label"
              :value="item.book_category_id">
            </el-option>
         </el-select>
        </el-form-item>
        <el-form-item label="类别名称" prop="cname" hidden>
          <el-input type="textarea" autosize v-model="addForm.cname" ></el-input>
        </el-form-item>
        <el-form-item label="描述" prop="description">
          <el-input type="textarea" autosize v-model="addForm.description"></el-input>
        </el-form-item>
        <el-form-item label="cata" prop="cata">
          <el-input type="textarea" autosize v-model="addForm.cata" ></el-input>
        </el-form-item>
        <el-form-item label="图书内容" prop="content">
          <el-input type="textarea" autosize v-model="addForm.content" ></el-input>
        </el-form-item>
         <el-form-item label="国际标准书号" prop="isbn">
          <el-input type="textarea" autosize v-model="addForm.isbn"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button style="margin-top:-70px" type="primary" @click="addBook">确 定</el-button>
     </span>
    </el-dialog>
    <!-- 详情对话框 -->
    <el-dialog
      title="图书详情"
      :visible.sync="infodialogVisible"
      width="50%" @close="infodialogClosed">
      <!-- 内容主体区域-->
      <el-form :model="infoForm" ref="infoFormRef" label-width="100px">
        <el-form-item style="margin-top:-30px">
          <span class="editbtn">
            <el-button type="info" @click="changeType">修 改</el-button>
          </span>
        </el-form-item >
        <el-form-item label="图书ID" prop="bookid" style="margin-top:-20px">
          <el-input type="textarea" autosize v-model="infoForm.bookid" disabled></el-input>
        </el-form-item>
        <el-form-item label="图书名称" prop="name">
          <el-input type="textarea" autosize v-model="infoForm.name" :disabled="isdisabled"></el-input>
        </el-form-item>
        <el-form-item label="图书简介" prop="outline">
          <el-input type="textarea" autosize v-model="infoForm.outline" :disabled="isdisabled"></el-input>
        </el-form-item>
        <el-form-item label="图书详情" prop="detail">
          <el-input type="textarea" autosize v-model="infoForm.detail" :disabled="isdisabled"></el-input>
        </el-form-item>
        <el-form-item label="出版社" prop="press">
          <el-input type="textarea" autosize v-model="infoForm.press" :disabled="isdisabled"></el-input>
        </el-form-item>
        <el-form-item label="出版时间" prop="publish_date">
           <el-input type="textarea" autosize v-model="infoForm.publish_date" :disabled="isdisabled"></el-input>
        </el-form-item>
        <el-form-item label="图书大小" prop="size">
          <el-input type="textarea" autosize v-model="infoForm.size" :disabled="isdisabled"></el-input>
        </el-form-item>
        <el-form-item label="图书版本" prop="version">
          <el-input type="textarea" autosize v-model="infoForm.version" :disabled="isdisabled"></el-input>
        </el-form-item>
        <el-form-item label="作者" prop="author">
          <el-input type="textarea" autosize v-model="infoForm.author" :disabled="isdisabled"></el-input>
        </el-form-item>
        <el-form-item label="翻译者" prop="translator">
          <el-input type="textarea" autosize v-model="infoForm.translator" :disabled="isdisabled"></el-input>
        </el-form-item>
        <el-form-item label="价格" prop="price">
          <el-input type="textarea" autosize v-model="infoForm.price" :disabled="isdisabled"></el-input>
        </el-form-item>
        <el-form-item label="总页数" prop="pages">
          <el-input type="textarea" autosize v-model="infoForm.pages" :disabled="isdisabled"></el-input>
        </el-form-item>
        <el-form-item label="目录" prop="catalog">
          <el-input type="textarea" autosize v-model="infoForm.catalog" :disabled="isdisabled"></el-input>
        </el-form-item>
        <el-form-item label="定价" prop="market_price">
          <el-input type="textarea" autosize v-model="infoForm.market_price" :disabled="isdisabled"></el-input>
        </el-form-item>
        <el-form-item label="会员价" prop="member_price">
          <el-input type="textarea" autosize v-model="infoForm.member_price" :disabled="isdisabled"></el-input>
        </el-form-item>
        <el-form-item label="折扣" prop="discount">
          <el-input type="textarea" autosize v-model="infoForm.discount" :disabled="isdisabled"></el-input>
        </el-form-item>
        <el-form-item label="图书图片" prop="image_url">
          <el-image
            style="width: 100px; height: 100px"
            :src="infoForm.image_url"
            :fit="fit"></el-image>
          <el-upload class="upload-demo"
            :action="null"
            :before-upload="beforeUpload"
            :on-preview="handlePreview"
            :on-remove="handleRemove"
            list-type="picture">
            <el-button size="small" type="primary" :disabled="isdisabled">点击上传</el-button>
            <div slot="tip" class="el-upload__tip">只能上传jpg/png文件，且不超过500kb</div>
          </el-upload>
        </el-form-item>
        <el-form-item label="库存数量" prop="store_mount">
          <el-input type="textarea" autosize v-model="infoForm.store_mount" :disabled="isdisabled"></el-input>
        </el-form-item>
        <el-form-item label="封装方式" prop="pack_style">
          <el-input type="textarea" autosize v-model="infoForm.pack_style" :disabled="isdisabled"></el-input>
        </el-form-item>
        <el-form-item label="上架状态" prop="is_shelf">
         <el-input type="textarea" autosize v-model="infoForm.is_shelf" placeholder="1:上架，0：下架" :disabled="isdisabled"></el-input>
        </el-form-item>
        <el-form-item label="类别ID" prop="book_category_id">
          <el-input type="textarea" autosize placeholder="1哲学宗教,2科学,3政治法律,4军事,5经济:,6文化教育体育,7历史,8医药卫生,9计算机" v-model="infoForm.book_category_id" :disabled="isdisabled"></el-input>
        </el-form-item>
        <el-form-item label="类别名称" prop="cname" :hidden = "!isdisabled">
          <el-input type="textarea" autosize v-model="infoForm.cname" :disabled="isdisabled"></el-input>
        </el-form-item>
        <el-form-item label="描述" prop="description">
          <el-input type="textarea" autosize v-model="infoForm.description" :disabled="isdisabled"></el-input>
        </el-form-item>
        <el-form-item label="cata" prop="cata">
          <el-input type="textarea" autosize v-model="infoForm.cata" :disabled="isdisabled"></el-input>
        </el-form-item>
        <el-form-item label="图书内容" prop="content">
          <el-input type="textarea" autosize v-model="infoForm.content" :disabled="isdisabled"></el-input>
        </el-form-item>
         <el-form-item label="国际标准书号" prop="isbn">
          <el-input type="textarea" autosize v-model="infoForm.isbn" :disabled="isdisabled"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer" :hidden="isdisabled">
        <el-button style="margin-top:-70px" type="primary" @click="editBookInfo">确 定</el-button>
     </span>
    </el-dialog>
  </div>
</template>

<script>
export default {
  data () {
    return {
      // 状态选项
      options: [{
        is_shelf: '1',
        label: '上架'
      }, {
        is_shelf: '0',
        label: '下架'
      }],
      is_shelf: '',
      // 类别选项
      options1: [{
        book_category_id: '1',
        label: '哲学宗教类'
      }, {
        book_category_id: '2',
        label: '科学类'
      }, {
        book_category_id: '3',
        label: '政治法律类'
      }, {
        book_category_id: '4',
        label: '军事类'
      }, {
        book_category_id: '5',
        label: '经济类'
      }, {
        book_category_id: '6',
        label: '文化教育体育类'
      }, {
        cate: '7',
        label: '历史类'
      }, {
        book_category_id: '8',
        label: '医药卫生类'
      }, {
        book_category_id: '9',
        label: '计算机类'
      }
      ],
      book_category_id: '',
      // 获取用户列表的参数对象
      queryInfo: {
        store_id: window.sessionStorage.getItem('storeid'),
        // 当前页数
        pagenum: 1,
        // 当前每页显示多少条数据
        pagesize: 5,
        query: ''
      },
      booklist: [],
      total: 0,
      // 控制添加对话框的显示和隐藏
      addDialogVisible: false,
      infodialogVisible: false,
      isdisabled: true,

      // 添加图书表单数据
      addForm: {
        book_category_id: '',
        store_id: '',
        name: '',
        outline: '',
        press: '',
        publish_date: '',
        detail: '',
        size: '',
        version: '',
        author: '',
        translator: '',
        price: '',
        pages: '',
        catalog: '',
        market_price: '',
        member_price: '',
        discount: '',
        image_url: '',
        store_mount: '',
        pack_style: '',
        is_shelf: '',
        cname: '',
        description: '',
        cata: '',
        content: '',
        isbn: ''
      },
      // 详情图书表单数据
      infoForm: {
        bookid: '',
        book_category_id: '',
        name: '',
        outline: '',
        press: '',
        publish_date: '',
        detail: '',
        size: '',
        version: '',
        author: '',
        translator: '',
        price: '',
        pages: '',
        catalog: '',
        market_price: '',
        member_price: '',
        discount: '',
        image_url: '',
        store_mount: '',
        pack_style: '',
        is_shelf: '',
        cname: '',
        description: '',
        cata: '',
        content: '',
        isbn: ''
      },
      // 添加表单的验证规则对象
      addFormRules: {
        name: [
          { required: true, message: '请输入图书名称', trigger: 'blur' }
        ],
        outline: [
          { required: true, message: '请输入图书简介', trigger: 'blur' }
        ],
        press: [
          { required: true, message: '请输入出版社', trigger: 'blur' }
        ],
        publish_date: [
          { required: true, message: '请输入出版时间', trigger: 'blur' }
        ],
        detail: [
          { required: true, message: '请输入图书详情', trigger: 'blur' }
        ],
        author: [
          { required: true, message: '请输入图书作者', trigger: 'blur' }
        ],
        price: [
          { required: true, message: '请输入图书价格', trigger: 'blur' }
        ],
        book_category_id: [
          { required: true, message: '请选择图书类别', trigger: 'blur' }
        ],
        is_shelf: [
          { required: true, message: '请选择图书上架信息', trigger: 'blur' }
        ],
        pages: [
          { required: true, message: '请输入总页数', trigger: 'blur' }
        ],
        store_mount: [
          { required: true, message: '请输入库存量', trigger: 'blur' }
        ],
        market_price: [
          { required: true, message: '请输入定价', trigger: 'blur' }
        ],
        member_price: [
          { required: true, message: '请输入会员价', trigger: 'blur' }
        ],
        discount: [
          { required: true, message: '请输入折扣', trigger: 'blur' }
        ]
      },
      fileList: [{ name: '', url: '' }]
    }
  },
  created () {
    this.getBookList()
  },
  methods: {
    // 上传图片
    async beforeUpload (image) {
      const pic = new FormData()
      pic.append('picture', image)
      const { data: res } = await this.axios.post('/UpPicture', pic)
      console.log(res)
      if (res.status !== '1') return this.$message.error('上传失败')
      this.$message.success('成功')
      this.addForm.image_url = res.url
      return false
    },
    // 获取图书列表
    async getBookList () {
      // 获取的请求
      console.log(this.queryInfo.pagesize)
      const { data: res } = await this.axios.post('/selectBook', {
        store_id: this.queryInfo.store_id,
        // store_id: 1,
        pagenum: this.queryInfo.pagenum,
        pagesize: this.queryInfo.pagesize
      })
      console.log(res)
      if (res.status !== '1') return this.$message.error('获取图书列表失败')
      this.$message.success('获取图书列表成功')
      this.booklist = res.data
      this.total = parseInt(res.sum)
      this.booklist.forEach((item, i) => {
        if (res.data[i].is_shelf === 1) {
          res.data[i].is_shelf = '上架'
        } else {
          res.data[i].is_shelf = '下架'
        }
      })
    },
    // 模糊查询图书
    async selectBookQuery () {
      // 获取的请求
      console.log(this.queryInfo.pagesize)
      const { data: res } = await this.axios.post('/selectBookQuery', {
        store_id: this.queryInfo.store_id,
        // store_id: 1,
        pagenum: this.queryInfo.pagenum,
        pagesize: this.queryInfo.pagesize,
        query: this.queryInfo.query
      })
      console.log(res)
      if (res.status !== '1') return this.$message.error('模糊获取图书列表失败')
      this.$message.success('模糊获取图书列表成功')
      this.booklist = res.data
      this.total = parseInt(res.sum)
      this.booklist.forEach((item, i) => {
        console.log(res.data[i].author)
        console.log(res.data[i].is_shelf)
        if (res.data[i].is_shelf === 1) {
          res.data[i].is_shelf = '上架'
        } else {
          res.data[i].is_shelf = '下架'
        }
      })
    },
    // 监听pagesize改变
    handleSizeChange (newSize) {
      console.log(newSize)
      this.queryInfo.pagesize = newSize
      this.getBookList()
    },
    // 监听页码值改变的事件
    handleCurrentChange (newPage) {
      console.log(newPage)
      this.queryInfo.pagenum = newPage
      this.getBookList()
    },
    // 监听添加对话框的关闭
    addDialogClosed () {
      this.$refs.addFormRef.resetFields()
    },
    // 监听修改表单的关闭事件
    infodialogClosed () {
      this.changeType()
      this.$refs.infoFormRef.resetFields()
    },
    // 添加图书请求
    addBook () {
      this.addForm.store_id = window.sessionStorage.getItem('storeid')
      this.addForm.cname = this.options1[this.addForm.book_category_id - 1].label
      this.$refs.addFormRef.validate(async valid => {
        console.log(this.addForm)
        if (!valid) return
        // 添加图书网络请求
        const { data: res } = await this.axios.post('/addBook', this.addForm)
        console.log(this.axios.post)
        console.log(res)
        if (res.status !== '1') {
          this.$message.error('添加失败')
        }
        this.$message.success('添加成功')
        // 隐藏添加图书对话框
        this.addDialogVisible = false
        // 重置添加列表
        this.$refs.addFormRef.resetFields()
        // 刷新图书列表
        this.getBookList()
      })
    },

    // 展示图书详情的对话框
    async showinfoDialog (id) {
      this.infodialogVisible = true
      console.log(id)
      const { data: res } = await this.axios.post('/selectBookinfo', {
        bookid: id
      }
      )
      if (res.status !== '1') {
        return this.$message.error('查询图书信息失败')
      }
      console.log(res)
      this.infoForm.author = res.author
      this.infoForm.bookid = res.bookid
      this.infoForm.image_url = res.image_url
      this.infoForm.is_shelf = res.is_shelf
      this.infoForm.name = res.name
      this.infoForm.outline = res.outline
      this.infoForm.press = res.press
      this.infoForm.price = res.price
      this.infoForm.publish_date = res.publish_date
      this.infoForm.detail = res.detail
      this.infoForm.size = res.size
      this.infoForm.version = res.version
      this.infoForm.translator = res.translator
      this.infoForm.pages = res.pages
      this.infoForm.catalog = res.catalog
      this.infoForm.market_price = res.market_price
      this.infoForm.member_price = res.member_price
      this.infoForm.discount = res.discount
      this.infoForm.store_mount = res.store_mount
      this.infoForm.pack_style = res.pack_style
      this.infoForm.cname = res.cname
      this.infoForm.book_category_id = res.book_category_id
      this.infoForm.description = res.description
      this.infoForm.cata = res.cata
      this.infoForm.content = res.content
      this.infoForm.isbn = res.isbn
    },

    // 修改图书信息并提交
    editBookInfo () {
      // this.changeType()
      this.$refs.infoFormRef.validate(async valid => {
        if (!valid) return
        // 发起修改请求
        const { data: res } = await this.axios.post('/modifyBook', {
          book_id: this.infoForm.bookid,
          book_category_id: this.infoForm.book_category_id,
          store_id: window.sessionStorage.getItem('storeid'),
          name: this.infoForm.name,
          outline: this.infoForm.outline,
          detail: this.infoForm.detail,
          press: this.infoForm.press,
          author: this.infoForm.author,
          publish_date: this.infoForm.publish_date,
          size: this.infoForm.size,
          version: this.infoForm.version,
          translator: this.infoForm.translator,
          isbn: this.infoForm.isbn,
          price: this.infoForm.price,
          pages: this.infoForm.pages,
          catalog: this.infoForm.catalog,
          market_price: this.infoForm.market_price,
          member_price: this.infoForm.member_price,
          discount: this.infoForm.discount,
          image_url: this.infoForm.image_url,
          store_mount: this.infoForm.store_mount,
          pack_style: this.infoForm.pack_style,
          is_shelf: this.infoForm.is_shelf,
          cname: this.infoForm.cname,
          description: this.infoForm.description,
          cata: this.infoForm.cata,
          content: this.infoForm.content
        })
        console.log(res)
        if (res.status !== '1') {
          this.$message.error('修改失败')
        }
        // 提示修改成功
        this.$message.success('修改成功')
        // 关闭对话框
        this.infodialogVisible = false
        // 刷新数据列表
        this.getBookList()
      })
    },

    // 根据ID删除
    async removeBookbyId (id) {
      // 弹框询问用户
      const confirmresult = await this.$confirm('此操作将永久删除该图书, 是否继续?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).catch(err => err)
      if (confirmresult !== 'confirm') {
        return this.$message.info('已取消删除')
      }
      const { data: res } = await this.axios.post('/deleteBook', {
        bookid: id
      })
      if (res.status !== '1') {
        return this.$message.error('删除失败')
      }
      this.$message.success('删除成功')
      this.getBookList()
    },

    // 修改开启
    changeType () {
      this.isdisabled = !this.isdisabled
    },
    //
    handleRemove (file, fileList) {
      console.log(file, fileList)
    },
    //
    handlePreview (file) {
      console.log(file)
    }
  }
}
</script>

